<template>
    <div>
        
        <van-nav-bar
        :title="$route.name"
        />
        <div class="content">
            <div class="left" ref="left">
                <ul>
                    <li v-for="(item,index) in left" :key="index"  :class="{current:index==currentIndex}" @click="selectItem(index,$event)">
                        <span class="left-item">{{item}}</span>
                    </li>
                </ul>
            </div>
            <div class="right" ref="right">
                <ul>
                    <!--  -->
                    <li v-for="(item,index) in right" :key="index" class="right-item right-item-hook">
                        <h2>{{item.name}}</h2>
                        <ul>
                            <li v-for="num in item.content" :key="num">
                                <div>
                                    <router-link to="/details" tag="a">{{item.name + num}}</router-link>
                                </div>
                            </li>
                        </ul>
                    </li>
                    
                </ul>
            </div>
        </div>
    </div>
</template>

<script>
import BScroll from "@better-scroll/core";
    export default {
        data:function(){
            return {
                left:["","","","Redmi","",""],
                right:[
                    {name:"",content:[1,2,3,4,5,6]},
                    {name:"",content:[1,2,3,4]},
                    {name:"",content:[1,2,3,4,5]},
                    {name:"Redmi",content:[1,2,3]},
                    {name:"",content:[1,2,3,4]},
                    {name:"",content:[1,2,3]}
                ],
                scrollY:0,
                listHeight:[]  //li
            }
        },
        methods: {
                _initScroll(){
                    // 
                    this.lefts = new BScroll(this.$refs.left,{
                        click:true, //
                        probeType:3 //
                    });
                    this.rights = new BScroll(this.$refs.right,{
                        click:true, //
                        probeType:3 //
                    });

                    // right
                    this.rights.on("scroll",(pos)=>{
                        this.scrollY = Math.abs(Math.round(pos.y));
                    })
                },
                _getHeight(){
                    let rightItems = this.$refs.right.getElementsByClassName("right-item-hook");
                    let height = 0;//
                    this.listHeight.push(height); //
                    // 
                    for(let i = 0;i < rightItems.length;i++){
                        let item = rightItems[i];
                        height += item.clientHeight; // 
                        this.listHeight.push(height);
                    }
                    
                },
                // 
                // index 
                // event 
                selectItem(index,event){
                    // better-scrolleventevent_constructed
                    // _constructedreturn
                    if(!event._constructed){
                        return;
                    }else{
                        // 
                        let rightItems = this.$refs.right.getElementsByClassName("right-item-hook");
                        let el = rightItems[index];
                        // scrollToElement()//
                        this.rights.scrollToElement(el);

                        // 
                        let leftItems = this.$refs.left.getElementsByTagName("li");
                        let elLi = leftItems[index];
                        this.lefts.scrollToElement(elLi);


                    }
                }
            },
            // 
            mounted() {  //
                // html
                // 
                // $nextTick() DOM
                
                this.$nextTick(()=>{
                    this._initScroll();//
                    this._getHeight(); //

                    // 
                    if("id" in this.$route.query){
                        // 
                        let rightItems = this.$refs.right.getElementsByClassName("right-item-hook");
                        let el = rightItems[this.$route.query.id];
                        this.rights.scrollToElement(el);
                    }

                })
                    

            },
            // 
            computed: {
                currentIndex(){
                    for(let i = 0; i < this.listHeight.length;i++){
                        let height = this.listHeight[i];
                        let height2 = this.listHeight[i+1];
                        // height2heightheight2
                        if(!height2 || (this.scrollY >=height && this.scrollY < height2) ){
                        
                            // 
                            let leftItems = this.$refs.left.getElementsByTagName("li");
                            let elLi = leftItems[i];
                            this.lefts.scrollToElement(elLi);
                            return i;
                        }
                    }
                    // this.listHeight0
                    return 0;
                }
            },
    }
</script>

<style lang="scss" scoped>
// scoped 
        .content{
            display: flex;
            position: absolute;
            /*  */
            top:46px;
            bottom: 50px;
            background-color: #eeeeee;
            width: 100%;
            overflow: hidden;
        }
        .left{
            flex:0 0 80px;
            width: 80px;
            background-color: #C8EBFA;
        }
        .left li{
            width:100%;
            height: 100%;
            font-size:14px;
        }
        .left li.current{
            background-color: #007AAE;
            color:#fff;
            font-weight: bold;
        }
        .left-item{
            display: block;
            width: 100%;
            height: 100%;
            line-height: 50px;
            height: 50px;
            text-align: center;
        }
        .right{
            flex:1;
            background-color: azure;
        }
        .right-item li{
            width: 100%;
            height: 100px;
            line-height: 100px;
            text-align: center;
            border: 1px solid yellow;
        }
</style>

